@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin:ital@1&display=swap);
body {
  margin: 0;
  font-family: 'Cabin', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: 'Cabin', sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
color:#292b2c;
}

input {
  width: 70%;
}

.sub-btn {
  width: 60%;
}

.fav-btn {
  border: none;
}

.carusel-photo {
  max-height: 45vh;
  object-fit: cover;
}

.list1 {
  width: 100%;
}
.first-line {
  border: none;
  vertical-align: middle;

}
.filter-div {
  display: flex;
  justify-content: center;
  padding: 1rem 0;
}

.filter {
  text-indent: 1px;
  text-overflow: '';
  border: none;
}
.down {
  position: fixed;
  bottom: 0;
  text-align: center;
  color: rgb(124, 121, 121);
}

.down-link {
  color: rgb(124, 121, 121);
}

down-link:hover {
  color: #333;
}
.log {
  font-size: 1.5em;
  font-weight: bold;
  border:none;
  padding: 0;
}

@media (min-width: 1024px) {
  .loged {
    margin: 0.1rem 3rem 0 2rem;
    font-size: 1.5rem;
  }

  #favs {
    margin: 0.3rem 2rem 0 2rem;
  }
}
